function convertNative(dt) {
  const datePattern = /^(\d{2})-(\d{2})-(\d{4})$/;
  const [, year, month, day] = datePattern.exec(dt);
  return new Date(`${month}, ${day} ${year}`);
}
export const Datetime = {
  getDisplayFromDate: function(datetime) {
    if (datetime === "Present") {
      return datetime;
    }
    return datetime;
  }
};
