import jsPDF from "/web_modules/jspdf.js";
function generatePDF(resume) {
  let {name, label, email, phone, website, summary} = resume.basics;
  const MARGIN = 20;
  var doc = new jsPDF("p", "pt", "a4");
  let cursorY = MARGIN;
  const newLineY = function(extra = 0) {
    let space = cursorY + 10 + extra;
    if (space + MARGIN > doc.internal.pageSize.height) {
      doc.addPage();
      return cursorY = MARGIN;
    }
    return cursorY = space;
  };
  let aqui = window.document.getElementsByName("meimage")[0];
  if (!aqui) {
    aqui = new Image();
    aqui.src = "/images/me.jpg";
  }
  doc.setFillColor(100, 100, 100);
  doc.rect(doc.internal.pageSize.width - MARGIN - 30, MARGIN + 2, 30, 30, "F");
  doc.addImage(aqui, "JPEG", doc.internal.pageSize.width - MARGIN - 30 - 2, MARGIN, 30, 30, "monkey");
  doc.setFontSize(22).setTextColor(255, 0, 0).setFont("times").setFontType("italic").text(name, doc.internal.pageSize.width - MARGIN - 30 - 10, MARGIN + 15, null, null, "right");
  doc.setTextColor(10, 10, 10).setFontSize(10).text(label, doc.internal.pageSize.width - MARGIN - 30 - 10, MARGIN + 30, null, null, "right").text(email, MARGIN, newLineY()).text(phone, MARGIN, newLineY()).text(website, MARGIN, newLineY());
  doc.setFontType("italic").text("About", MARGIN, newLineY(10));
  doc.line(20, cursorY + 2, doc.internal.pageSize.width - MARGIN, cursorY + 2).text(doc.splitTextToSize(summary, doc.internal.pageSize.width - 2 * MARGIN), MARGIN, newLineY(10));
  newLineY(20);
  doc.setFontSize(10).setFont("times").setFontType("italic").text("Education", MARGIN, newLineY());
  doc.line(20, cursorY + 2, doc.internal.pageSize.width - MARGIN, cursorY + 2);
  newLineY();
  resume.education.map((x) => {
    const {institution, area, startDate, endDate} = x;
    doc.setFontSize(10).setFont("times").setFontType("bold").text(institution + " " + area, MARGIN + 10, newLineY(5)).setFontType("italic").text(`${startDate} - ${endDate}`, doc.internal.pageSize.width - MARGIN, cursorY, null, null, "right");
  });
  newLineY(10);
  doc.text("Skills", MARGIN, newLineY());
  doc.line(20, cursorY + 2, doc.internal.pageSize.width - MARGIN, cursorY + 2);
  newLineY();
  resume.skills.map((x) => {
    const {title, description, keywords = []} = x;
    doc.setFontSize(10).setFont("times").setFontType("bold").text(title, MARGIN + 10, newLineY(5)).setFontType("italic").text(`${description}`, MARGIN + 105, cursorY);
    doc.text(keywords.join(" | "), doc.internal.pageSize.width - MARGIN, cursorY, null, null, "right");
  });
  newLineY(10);
  doc.setFontType("italic").text("Experience", MARGIN, newLineY());
  doc.line(20, cursorY + 2, doc.internal.pageSize.width - MARGIN, cursorY + 2);
  newLineY();
  resume.works.map((x) => {
    const {company, position, startDate, endDate = "current", highlights} = x;
    doc.setTextColor(10, 10, 10).setFontSize(10).setFont("times").setFontType("bold").text(company, MARGIN + 10, newLineY(10));
    doc.setTextColor(175, 10, 10).setFontType("italic").text(position, MARGIN + 130, cursorY);
    doc.setTextColor(10, 10, 10).text(`${startDate} - ${endDate}`, doc.internal.pageSize.width - MARGIN, cursorY, null, null, "right");
    highlights.map((x2) => {
      doc.text(x2, MARGIN + 20, newLineY());
    });
  });
  doc.save(`resume-${name}.pdf`);
}
function generateCoverPDF(resume) {
  let {name, cover} = resume.basics;
  const MARGIN = 40;
  var doc = new jsPDF("p", "pt", "letter");
  let cursorY = MARGIN;
  const newLineY = function(extra = 0) {
    let space = cursorY + 10 + extra;
    if (space + MARGIN > doc.internal.pageSize.height) {
      doc.addPage();
      return cursorY = MARGIN;
    }
    return cursorY = space;
  };
  doc.setFontSize(12).setFont("times").setFontType("italic").text(doc.splitTextToSize(cover, doc.internal.pageSize.width - 2 * MARGIN), MARGIN, newLineY(30));
  doc.save(`cover-${name}.pdf`);
}
export {generatePDF, generateCoverPDF};
